import React, {useState} from 'react'
import { Card, Table, Button, Tag, message, Menu } from 'antd';
import UserView from './UserView';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusCircleOutlined } from '@ant-design/icons';
import AvatarStatus from 'components/shared-components/AvatarStatus';
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown';
import Flex from 'components/shared-components/Flex'
import NumberFormat from 'react-number-format';
import { useHistory } from "react-router-dom";
import { DataService } from "services/employee.service";
import { Typography } from "antd";

const { Title } = Typography;

const getUserStatus = status => {
	if(status) {
		return <Tag className ="text-capitalize" color='cyan'>Active</Tag>
	}
	else{
		return <Tag className ="text-capitalize" color='red'>Blocked</Tag>
	}
}

const ProductList = ({systemData}) => {
	let history = useHistory();
	const [userProfileVisible, setUserProfileVisible] = useState(false)
	const [selectedUser, setSelectedUser] = useState(null)
	const { employees, employeesLoading, site, sitsLoading } = systemData;
	const dropdownMenu = row => (
		<Menu>
			
			<Menu.Item onClick={() => {viewDetails(row)}}>
				<Flex alignItems="center">
					<EditOutlined />
					<span className="ml-2">Edit Details</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => deleteRow(row)}>
				<Flex alignItems="center">
					<DeleteOutlined />
					<span className="ml-2">Delete</span>
				</Flex>
			</Menu.Item>
		</Menu>
	);
	
	const addProduct = () => {
		history.push(`/app/apps/sitesManagers/add`)
	}
	
	const viewDetails = row => {
		history.push(`/app/apps/sitesManagers/edit/${row._id}`)
	}
	const handleMenuClick = data => {
		let status = data.status && data.status === true ? false : true;
		let displayData = status ? data.name + ' Activate' : data.name +' Blocked';
		data.status = status;
		DataService.update(data._id,data);
		message.success(`Site Manager ${displayData}`);
		closeUserProfile();
	};
	const showUserProfile = userInfo => {
		userInfo.storeName = !sitsLoading ? (site[userInfo.site] ? site[userInfo.site].name : userInfo.backupSiteName):(<LoadingOutlined/>)
		setUserProfileVisible(true)
		setSelectedUser(userInfo)
	};
	
	const closeUserProfile = () => {
		setUserProfileVisible(false)
		setSelectedUser(null)
	}
	const deleteRow = row => {
		DataService.remove(row._id)
		message.success({ content: `Site Manager ${row.name} deleted `, duration: 3 });
	}

	const tableColumns = [
		{
			title: 'Full Name',
			dataIndex: 'name',
			render: (_, record) => (
				<div className="d-flex" onClick={() => {showUserProfile(record.data())}} style={{cursor:'pointer'}}>
					<AvatarStatus size={60} type="square" src={record.data().profile} name={record.data().name} subTitle={record.data().gender}/>
				</div>
			),
		},
		{
			title: 'Phone',
			dataIndex: 'phoneNumber',
			render: (_, record) => (
				<div>
					<NumberFormat
						displayType={'text'} 
						value={record.data().phoneNumber} 
						prefix={'0'} 
						thousandSeparator={false} 
					/>
				</div>
			),
		},
		{
			title: 'Site',
			dataIndex: 'site',
			render: (_, record) => !sitsLoading ? (site[record.data().site] ? site[record.data().site].name : record.data().backupSiteName):(<LoadingOutlined/>)
		},
		{
			title: 'Status',
			dataIndex: 'status',
			render: (_, record) => (
				<Flex alignItems="center">{getUserStatus(record.data().status)}</Flex>
			),
		},
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<EllipsisDropdown menu={dropdownMenu(elm.data())}/>
				</div>
			)
		}
	];

	return (
		<Card>
			<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
				<Flex className="mb-1" mobileFlex={false}>
					<div className="mr-md-3 mb-3">
						<Title level={4}>Site Managers</Title>
					</div>
				</Flex>
				<div>
					<Button onClick={addProduct} size="small" type="primary" icon={<PlusCircleOutlined />} block>Add New</Button>
				</div>
			</Flex>
			<div className="table-responsive">
				<Table 
					columns={tableColumns} 
					dataSource={!employeesLoading ? employees.docs : []} 
					loading={employeesLoading}
					rowKey='_id' 
				/>
			</div>
			<UserView data={selectedUser} visible={userProfileVisible} handleMenuClick={handleMenuClick} close={()=> {closeUserProfile()}}/>
		</Card>
	)
}

export default ProductList
